<template>
  <div>
    <el-card style="width: 400px; height: 400px; display: inline-block; margin: 10px;">
      <div slot="header">
        文件（点击文件名下载）
      </div>
      <div style="height: 300px; position: relative">
          <div v-if="loading">
            <p>正在读取文件...</p>
          </div>
          <div v-else>
            <files :share="response" v-if="response"></files>
            <div v-else>
              <p>{{ errorMsg }}</p>
            </div>
          </div>
          <div style="position: absolute; bottom: 0px; width: 100%;">
            <el-link type="primary" @click="gotoUpload">使用SendFile共享文件</el-link>
          </div>
      </div>
    </el-card>
  </div>
</template>
<script>
import axios from 'axios'
import files from './Files'
export default {
  components: {
    files
  },
  data() {
    return {
      // files: [],
      response: null,
      loading: true,
      errorMsg: null
    }
  },
  methods: {
    gotoUpload() {
      this.$router.push('/')
    }
  },
  created() {
    const _this = this
    axios.get(
      `/api/shares/${this.$route.params.shareId}`
    ).then(
      function (data) {
        _this.response = data.data
      }, function (err) {
        _this.errorMsg = err.response.data.message
      }
    ).finally(function() {
        _this.loading = false;
    })
  }
}
</script>
